import guid from 'guid';

import CLASS from './deck_classes';

export default buildDeck;

function buildDeck (teamData) {
    const deck = [];
    let specialCount = 0;

    teamData.cards.forEach(card => {
        specialCount++;
        deck.push(new Card(card));
    });

    console.log(`added ${specialCount} special cards`)

    const basicCards = CLASS[teamData.deckClass];

    if (basicCards) {
        let basicCount = 0;
        basicCards.forEach(card => {
            for (let i = 0; i < card.qty; i++) {
                basicCount++;
                deck.push(new Card(card));
            }
        });
        console.log(`added ${basicCount} basic cards`)
    } else {
        console.warn(`Failed to generate Basic cards for ${teamData.characters}; Deck class: "${teamData.deckClass}"`)
    }

    console.log(`generated deck of ${deck.length} cards`);
    return shuffle(deck);
}

function shuffle(array) {
    var currentIndex = array.length, temporaryValue, randomIndex;
  
    // While there remain elements to shuffle...
    while (0 !== currentIndex) {
  
      // Pick a remaining element...
      randomIndex = Math.floor(Math.random() * currentIndex);
      currentIndex -= 1;
  
      // And swap it with the current element.
      temporaryValue = array[currentIndex];
      array[currentIndex] = array[randomIndex];
      array[randomIndex] = temporaryValue;
    }
  
    return array;
  }

function Card (opts) {
    this.guid = guid.raw();
    this.name = opts.name || 'Basic';
    this.character = opts.owner;
    this.info = opts.info;
    this.atk = opts.attack || 0;
    this.def = opts.defense || 0;

    if (opts.info == null) delete this.info;
}




